import { useContext, useEffect, useState } from "react";
import Modal from "react-bootstrap/Modal";
import { toast } from "react-toastify";
import User from "../ContextApi";
import { useFormik } from "formik";
import { signUpSchema } from "../schemas";

const initialState = {
    name : "",
    phone : "",
    address1  :"",
    pin : "",
    state : "",
    city : "",
    country : "",
    year : "",
    college : "",
    degree : ""
}

const CourseEnroll = ({ show, setShow }) => {
  const [radioValue, setRadioValue] = useState(1);
  const [step, setstep] = useState(1);


const {values,errors,touched,handleBlur,handleChange,handleSubmit} =  useFormik({
    initialValues : initialState,
    validationSchema : signUpSchema,
    onSubmit : (values) =>{
      formValidator(values)
    }
  });

  const { userDetails, setUserDetails } = useContext(User);


  const nextStep = () => {
    setstep((step) => step + 1);
  };

  const prevStep = () => {
    setstep((step) => step - 1);
  };

  const handleClose = () => setShow(false);

  const formValidator = async () =>{
   const response = await fetch(`${process.env.REACT_APP_WEB_SERVICE}/user/saveUserDetails`,{
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({...values,email : userDetails.email}),
    })

    const data = await response.json();

    if(data.error){
        toast.error("Some error occured")
    }else{
        toast.success("Details saved successfully")
        setUserDetails(prevState =>{
            const newState = {...prevState};
            newState.saved = data.saved;
            return newState
        })
        handleClose();
    }
 
  }

  useEffect(() =>{
setstep(1)

  },[show])

  return (
    <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false}>
      <form onSubmit={(e) =>{
        e.preventDefault();
        handleSubmit()}
      } >
      <Modal.Header closeButton>
        <Modal.Title>
        {step === 1 ?  <span>Welcome to Ninja Family!</span> : <>
           <div className="congratulations">Academic/Professional Details</div>
           <div className="border__underline">
             <div className="border__underline1"></div>
             <div className="border__underline2"></div>
           </div>
           </>
        }
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        
        {step === 1 ?         <div style={{ height: "20rem", overflowY: "auto",padding : "16px" }}>
          <div className="congratulations">Congratulations</div>
          <div className="border__underline">
            <div className="border__underline1"></div>
            <div className="border__underline2"></div>
          </div>
          <div>You have successfully signed for a free trial of the course</div>
          <div className="congratulations personal__details">
            Personal Details
          </div>
          <div className="border__underline">
            <div className="border__underline1"></div>
            <div className="border__underline2"></div>
          </div>
          <div className="d-flex align-items-center justify-content-between indain">
            <div className="d-flex align-items-center">
              <input
                type="radio"
                id="indian"
                checked={radioValue === 1}
                onClick={() => setRadioValue(1)}
              />
              <div id="indian">I am an Indian Student</div>
            </div>
            <div className="d-flex align-items-center">
              <input
                type="radio"
                id="international"
                checked={radioValue === 2}
                onClick={() => setRadioValue(2)}
              />
              <div id="international">I am an International Student</div>
            </div>
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" name="name" placeholder="Name" value={values.name} onBlur={handleBlur} onChange={handleChange}  />
           {errors.name && touched.name && <span className="error">{errors.name}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="phone" name="phone" placeholder="Phone Number" value={values.phone} onBlur={handleBlur} onChange={handleChange}  />
            {errors.phone && touched.phone && <span className="error">{errors.phone}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" name="address1" placeholder="Address Line 1" value={values.address1} onBlur={handleBlur} onChange={handleChange}  />
            {errors.address1 && touched.address1 && <span className="error">{errors.address1}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" placeholder="Address Line 2" />
      
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="number" name="pin" placeholder="Pin Code" value={values.pin} onBlur={handleBlur} onChange={handleChange} />
            {errors.pin && touched.pin && <span className="error">{errors.pin}</span> }
          </div>
          <div className="d-flex justify-content-between">
            <div className="d-flex align-items-center flex-column input__register1">
              <input type="text" name="state" placeholder="State" value={values.state} onBlur={handleBlur} onChange={handleChange}  />
              {errors.state && touched.state && <span className="error">{errors.state}</span> }
            </div>
            <div className="d-flex align-items-center flex-column input__register1">
              <input type="text" name="city" placeholder="City" value={values.city} onBlur={handleBlur} onChange={handleChange}  />
              {errors.city && touched.city && <span className="error">{errors.city}</span> }
            </div>
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" name="country" placeholder="Country" value={values.country} onBlur={handleBlur} onChange={handleChange}  />
            {errors.country && touched.country && <span className="error">{errors.country}</span> }
          </div>
        </div> : <div style={{ height: "20rem", overflowY: "auto",padding : "16px" }}>
            <div> Help us understand more about you to help us deliever a tailored experience.</div>
            <div className="d-flex align-items-center flex-column input__register">
            <input type="number" name="year" placeholder="Graduation Year" value={values.year} onBlur={handleBlur} onChange={handleChange}  />
            {errors.year && touched.year && <span className="error">{errors.year}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" placeholder="Degree" name="degree" value={values.degree} onBlur={handleBlur} onChange={handleChange}  />
            {errors.degree && touched.degree && <span className="error">{errors.degree}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" placeholder="College Name" name="college" value={values.college} onBlur={handleBlur} onChange={handleChange}  />
            {errors.college && touched.college && <span className="error">{errors.college}</span> }
          </div>
          <div className="d-flex align-items-center flex-column input__register">
            <input type="text" placeholder="Company Name (Optional)" />
          </div>
            </div>}

      </Modal.Body>
      <Modal.Footer>
        {step > 1 && (
          <button className="login__button" onClick={prevStep}>
            Previous
          </button>
        )}

        {step === 2 ? (
          <input type="submit"  className="enroll__button" value="Okay" />
  
        ) : (
          <button className="enroll__button" onClick={nextStep}>
            Next
          </button>
        )}
      </Modal.Footer>
      </form>
    </Modal>
    
  );
};

export default CourseEnroll;
