import * as Yup from 'yup'; 

export const signUpSchema = Yup.object({
    name : Yup.string().min(2).max(25).matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    phone : Yup.number().typeError('Phone must be a number').required("Required*"),
    address1  :Yup.string().required("Required*"),
    pin : Yup.number().typeError('Pin must be a number').required("Required*"),
    state : Yup.string().matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    city : Yup.string().matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    country : Yup.string().matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    year : Yup.number().typeError('Year must be a number').required("Required*"),
    college :Yup.string().matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    degree : Yup.string().matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
})

export const contacUsSchema = Yup.object({
    name : Yup.string().min(2).max(25).matches(/^[aA-zZ\s]+$/, "Only alphabets are allowed").required("Required*"),
    phone : Yup.number().typeError('Phone must be a number').required("Required*"),
    year : Yup.number().typeError('Year must be a number').required("Required*"),
    email : Yup.string().email().required("Required*")
    
})