import { useContext, useEffect } from "react";
import { toast } from "react-toastify";
import User from "../ContextApi";

const Story = () => {
  const { userDetails, setUserDetails } = useContext(User);

  useEffect(() => {
    fetch(`${process.env.REACT_APP_WEB_SERVICE}/story`, {
      credentials: "include",
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.error) {
          toast.error(JSON.stringify(data.message));
        } else {
          setUserDetails((prevState) => {
            const newState = { ...prevState };
            newState.stories = data.data;
            return newState;
          });
        }
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (<>
  <div className="home__content">Coding Ninja Review By Students</div>
    <div className="stories__container">
      <div className="stories__section">
        {userDetails?.stories?.map((story) => {
          return (
            <div className="stories__details__container">
              <div>
                <img src={story.image} alt="" width="100%" height="100%" />
              </div>
              <div className="d-flex justify-content-between align-items-center">
                <div><img src="https://files.codingninjas.in/google-1-5663.png" alt="" width="100%" height="50px" /></div>
                <div className="story__review">Read More Reviews</div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
    </>
  );
};

export default Story;
