import { useContext, useEffect, useState } from "react";
import User from "../ContextApi";
import CourseCard from "./CourseCard";
import { courseDetails } from "../utility";
import CourseEnroll from "../Modals/CourseEnroll";

const Classroom = () =>{
    const {userDetails,setUserDetails} = useContext(User);
    const [show,setShow] = useState(true)

    useEffect(() =>{
        if(userDetails.saved === "True"){
            setShow(() => false)
        }else{
            setShow(() => true)
        }
        
    },[])
return <>
   <CourseEnroll
    show={show}
    setShow={setShow}
    />
 <div className="classroom__container">
<div className="classroom__section">
    <div className="courses__heading">My Courses</div>
    <div className="course__list2">
        {userDetails?.courses?.map(course =>{
            return <CourseCard
            type="Full Stack"
            name={course}
            duration={courseDetails[course].duration}
            other={courseDetails[course].others}
            imageUrl={courseDetails[course].imageUrl}
            isClassroom={true}
          />
        })}
    </div>
</div>
</div>
</>
}

export default Classroom;