import { useContext, useState } from "react";
import Modal from "react-bootstrap/Modal";
import User from "../ContextApi";
import { toast } from "react-toastify";

const initialState = {
  name: "",
  email: "",
  password: "",
};

const Login = ({ show, setShow }) => {
  const [formDetails, setFormDetails] = useState({
    type: "LOGIN",
    ...initialState,
  });

  const { userDetails, setUserDetails } = useContext(User);

  const handleClose = () => setShow(false);

  const handleClick = (key, value) => {
    setFormDetails((prevState) => {
      let newState = { ...prevState };
      if (value === "LOGIN" || value === "REGISTER") {
        newState = { ...prevState, ...initialState };
      }
      newState[key] = value;
      return newState;
    });
  };

  const formSubmitHandler = async () => {
    setUserDetails((prevState) => {
      return { ...prevState, isLoader: true };
    });
    if (formDetails.type === "LOGIN") {
      const response = await fetch(`${process.env.REACT_APP_WEB_SERVICE}/user/login`, {
        method: "POST",
        credentials: "include",
        referrerPolicy: "no-referrer",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formDetails),
      });
      const data = await response.json();
      if (!data.err) {
        if (!data.isUserLoggedIn) {
          setUserDetails((prevState) => {
            return { ...prevState, isLoader: false };
          });
          return toast.error("Invalid Credentials");
        }
        setUserDetails((prevState) => {
          const newState = { ...prevState };
          newState.isLoggedIn = data.isUserLoggedIn;
          newState.username = data.username;
          newState.email = data.email;
          newState.saved = data.saved;
          return newState;
        });
        handleClose();
      } else {

        toast.error(JSON.stringify(data?.message?.message));
      }
    } else {
      if (
        formDetails.name !== "" &&
        formDetails.email !== "" &&
        formDetails.password === ""
      ) {
        setUserDetails((prevState) => {
          return { ...prevState, isLoader: false };
        });
        return toast.error("Password is required");
      }

      const response = await fetch(`${process.env.REACT_APP_WEB_SERVICE}/user/register`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formDetails),
      });
      const data = await response.json();
      if (!data.error) {
        toast.success("User registered successfully");
        handleClose();
      } else {

        toast.error(JSON.stringify(data?.message?.message || "Email already exists"));
      }
    }
    setUserDetails((prevState) => {
      return { ...prevState, isLoader: false };
    });
  };

  return (
    <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false}>
      <Modal.Header closeButton>
        <Modal.Title>
          <span className="login__header">
            {formDetails.type === "LOGIN" ? "Login" : "Register"}
          </span>
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {formDetails.type === "REGISTER" && (
          <div className="form__field">
            <label htmlFor="username">Username</label>
            <input
              type="text"
              required
              id="username"
              name="username"
              value={formDetails.name}
              onChange={(e) => handleClick("name", e.target.value)}
            />
          </div>
        )}
        <div className="form__field">
          <label htmlFor="email">Email Id</label>
          <input
            type="email"
            required
            id="email"
            name="email"
            value={formDetails.email}
            onChange={(e) => handleClick("email", e.target.value)}
          />
        </div>
        <div className="form__field">
          <label htmlFor="password">Password</label>
          <input
            type="password"
            required
            id="password"
            name="password"
            value={formDetails.password}
            onChange={(e) => handleClick("password", e.target.value)}
          />
        </div>
        <div className="register__title">
          {formDetails.type === "LOGIN" ? (
            <span onClick={() => handleClick("type", "REGISTER")}>
              Create a new account
            </span>
          ) : (
            <span onClick={() => handleClick("type", "LOGIN")}>
              Back to login
            </span>
          )}
        </div>
      </Modal.Body>
      <Modal.Footer>
        <button className="login__button" onClick={handleClose}>
          Cancel
        </button>
        <button className="enroll__button" onClick={formSubmitHandler}>
          {formDetails.type === "LOGIN" ? "Login" : "Register"}
        </button>
      </Modal.Footer>
    </Modal>
  );
};

export default Login;
