import { useEffect, useState } from 'react';
import './App.css';
import Router from './Router';
import Header from './components/Header';
import User from './ContextApi';
import 'react-toastify/dist/ReactToastify.css';
import { Oval } from 'react-loader-spinner'
import { ToastContainer } from 'react-toastify';
import Footer from './components/Footer';
import ContactUs from './components/ContactUs';

function App() {
  const [userDetails, setUserDetails] = useState({
    isLoggedIn: false,
    isLoader: false,
    username: "",
    email: "",
    stories: [],
    courses: [],
    cart: {
      name: "",
      description: "",
      type: "",
      duration: "",
      others: "",
    },
    saved: "False"
  })


  useEffect(() => {
    fetch(`${process.env.REACT_APP_WEB_SERVICE}/getCookie`, { credentials: "include" })
      .then(res => res.json()).then(data => {
        if (!data.err && data.jwt !== "") {
          setUserDetails(prevState => {
            const newState = { ...prevState };
            newState.isLoggedIn = true;
            newState.username = data.username;
            newState.email = data.email;
            newState.courses = data.courses;
            newState.saved = data.saved;
            return newState;
          })
        }
      })

  }, [])

  return (
    <>
      <User.Provider value={{ userDetails, setUserDetails }}>
        <Header />
        <Router />
        <ContactUs />
        <ToastContainer />
        <Oval
          height={80}
          width={80}
          color="#ff9100"
          wrapperStyle={{}}
          wrapperClass="loader"
          visible={userDetails.isLoader}
          ariaLabel='oval-loading'
          secondaryColor="#feeee5"
          strokeWidth={2}
          strokeWidthSecondary={2}

        />
        <Footer />

      </User.Provider>

    </>
  );
}

export default App;
