const WorkExperienceOptions = ({updateExperienceTypeSelected}) =>{
return <div>
<div className="work__exp__heading1">
  What is your work experience?
</div>
<div className="work__exp__heading2">
  This will help us recommend the best programs for you.
</div>
<div className="d-flex justify-content-around media__work__options">
  <div className="work__exp__card margin__right__20" onClick={() => updateExperienceTypeSelected("STUDENT")}>
    <img
      src="https://files.codingninjas.com/student-24426.png"
      width="124px"
      height="130px"
      alt=""
    />
    <div className="d-flex justify-content-between work__exp__card__content1">
      <div>
        <div>No work</div>
        <div> experience</div>
      </div>
      <span>&gt;</span>
    </div>
    <div className="work__exp__card__content2">
      Designed as per college student schedule
    </div>
  </div>
  <div className="work__exp__card" onClick={() => updateExperienceTypeSelected("PROFESSIONAL")}>
    <img
      src="https://files.codingninjas.com/professional-24427.png"
      width="124px"
      height="130px"
      alt=""
    />
    <div className="d-flex justify-content-between work__exp__card__content1">
      <div>
        <div>Having work</div>
        <div> experience</div>
      </div>
      <span>&gt;</span>
    </div>
    <div className="work__exp__card__content2">
      Designed as per working professional schedule
    </div>
  </div>
</div>
</div>
}

export default WorkExperienceOptions;