import CourseCard from "./CourseCard";

const Courses = ({ experienceTypeSelected,updateExperienceTypeSelected }) => {
  return (
    <div className="courses__container">
      <div className="course__heading1">Courses Specially Curated for</div>
      <div className="course__heading2">
        {experienceTypeSelected === "STUDENT"
          ? "No Work Experience "
          : "Having Work Experience  "}
        <span className="course__heading3" onClick={() => updateExperienceTypeSelected("")}>Edit preference </span>
      </div>
      <div className="course__heading4">
        Recommended Courses for
        {experienceTypeSelected === "STUDENT" ? " students" : " professionals"}
      </div>
      <div className="course__list1">
        {experienceTypeSelected === "STUDENT" ? (
          <>
            <CourseCard
              type="Full Stack"
              name="MERN Stack"
              duration="140"
              other="6+ Projects"
              imageUrl="https://files.codingninjas.com/mern-11710.svg"
            />
            <CourseCard
              type="Data structures and algorithms"
              name="Basics of C++ with Data Structures and Algorithms "
              duration="60"
              other="350+ Problems"
              imageUrl="https://files.codingninjas.com/c-11714.svg"
            />
          </>
        ) : (
          <>
            <CourseCard
              type="Full Stack"
              name="MERN Full Stack Web Development"
              duration="170"
              other="6+ Projects"
              imageUrl="https://files.codingninjas.com/mern-22525.png"
            />
            <CourseCard
              type="Data structures and algorithms"
              name="Data Structures & Algorithms in Python"
              duration="60"
              other="350+ Problems"
              imageUrl="https://files.codingninjas.com/python-11712.svg"
            />
          </>
        )}
      </div>
    </div>
  );
};

export default Courses;
