import { useNavigate, useParams } from "react-router-dom";

const CourseCard = ({ type, name, duration, other, imageUrl, isClassroom }) => {

  const navigate = useNavigate();
  const params = useParams();

  const setCourseDetailsHandler = () => {

    navigate(`/checkout/${name}`)

  }

  return <div className="course__details__container" onClick={isClassroom ? null : setCourseDetailsHandler}>
    <div className="course__detail__heading__container">
      <div className="course__detail__heading1">{type}</div>
      <div className="d-flex justify-content-between align-items-center">
        <div className="course__detail__heading2">
          {name}
        </div>
        <img src={imageUrl} alt="" />
      </div>
    </div>
    <div className="d-flex course__detail__heading__containe2 ">
      <div className="d-flex align-items-center"><img src="https://files.codingninjas.in/vector-1-19336.svg" alt="" />&nbsp;{duration}+ Hours&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div>
      <div className="d-flex align-items-center"><img src="https://files.codingninjas.in/mdi_palette-swatch-19337.svg" alt="" />&nbsp;{other}</div>
    </div>
    <div className="course__rating__container"><span className="rating__span">4.8</span> <img src="https://files.codingninjas.in/star_vector-20760.png" alt="" />(17k+ students)</div>
  </div>
}

export default CourseCard;