import { useFormik } from "formik";
import { useState } from "react";
import { toast } from "react-toastify";
import { contacUsSchema } from "../schemas";

const initialState = {
  phone : "",
  name  :"",
  email : "",
  year : ""
  }

const ContactUs = () =>{
  const {values,errors,touched,handleBlur,handleChange,handleSubmit} =  useFormik({
    initialValues : initialState,
    validationSchema : contacUsSchema,
    onSubmit : (values) =>{
      requestCallHandler(values)
    }
  });


      const requestCallHandler =  async (values) =>{
        const response = await fetch(`${process.env.REACT_APP_WEB_SERVICE}/user/callback`,{
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(values),
      })
  
      const data = await response.json();
  
      if(data.error){
          toast.error("Some error occured")
      }else{
          toast.success("Calllback requested successfully")
      }
      }

    return <>
    <div className="d-flex align-items-center flex-column queries__container">
        <div className="queries__subcontainer">
          <div className="d-flex flex-column queries__content__container">
            <div className="queries__content1 margin__bottom__1">
              Have any queries?
            </div>
            <div className="queries__content2 margin__bottom__1">
              Get a free counselling session from our experts
            </div>
            <div className="d-flex">
              <div className="queries__image">
                <img
                  src="https://files.codingninjas.in/call-icon-9597.svg"
                  alt=""
                />
              </div>
              <div>
                <div className="queries__content2">
                  Call on our toll free number
                </div>
                <div className="queries__content1">1800-123-3598</div>
              </div>
            </div>
          </div>
          <form onSubmit={handleSubmit}>
          <div className="queries__form__container">
            <input type="number" placeholder="Contact Number*" name="phone" value={values.phone} onChange={handleChange} onBlur={handleBlur} />
           {errors.phone && touched.phone && <span className="error">{errors.phone}</span> } 
            <input type="text" placeholder="First Name*" name="name" value={values.name} onChange={handleChange} onBlur={handleBlur} />
            {errors.name && touched.name && <span className="error">{errors.name}</span> }
            <input type="email" placeholder="Email Address*" name="email" value={values.email} onChange={handleChange} onBlur={handleBlur} />
            {errors.email && touched.email && <span className="error">{errors.email}</span> }
            <input type="number" placeholder="Graduation Year*" name="year" value={values.year} onChange={handleChange} onBlur={handleBlur} />
            {errors.year && touched.year && <span className="error">{errors.year}</span> }
            <input type="submit" className="callback__button"  value="Request Callback" />
          
          </div>
          </form>
          <div></div>
        </div>
        <div className="queries__subcontainer2"></div>
      </div>
      </>
}

export default ContactUs;